import './index.less'
import Element from '@/lib/element'
import RangeSlider from '../rangeSlider'

class Display extends Element {
  constructor() {
    super()
    this.init()
  }

  init() {
    this.activeTabKey = 'register'
    this.bindTabs()

    this.memory = []
    this.memoryRange = [0, 6000]
    this.beforeMemoryRange = [0, 0]

    const slider = new RangeSlider('#memory-slider', {
      min: 0,
      max: 65534,
      step: 1000,
      value: this.memoryRange,
      onChange: (value) => {
        this.memoryRange = value
        this.updateMemory()
      },
    })

    this.initMemory()
  }

  bindTabs = () => {
    let tabs = document.querySelector('.display-stat-tabs')
    let tabItems = Array.from(tabs.children)
    let panes = document.querySelectorAll('.display-stat-tab-pane > div')
    panes = Array.from(panes)

    tabs.addEventListener('click', (e) => {
      let elt = e.target
      if (elt.parentElement === tabs) {
        for (let item of tabItems) {
          item.classList.remove('active')
        }

        elt.classList.add('active')

        let key = elt.dataset.key
        this.activeTabKey = key
        if (key === 'memory') {
          this.updateMemory()
        } else if (key === 'register') {
          this.updateRegister()
        }

        for (let p of panes) {
          let pkey = p.dataset.key
          if (pkey === key) {
            p.classList.add('active')
          } else {
            p.classList.remove('active')
          }
        }
      }
    })
  }

  updateRegister() {
    let registers = this.registers
    if (this.activeTabKey !== 'register') {
      return
    }

    let html = Object.entries(registers)
      .map(([key, value]) => {
        return `
        <div>
          <div class="register-name">${key}</div>
          <span>${value}</span>
        </div>
      `
      }).join('')
    let registerDom = document.querySelector('.display-stat-tab-pane div[data-key=register]')
    registerDom.innerHTML = html
  }

  initMemory() {
    let wrapper = document.querySelector('.display-stat-tab-pane .display-memory-content-wrapper')
    let content = wrapper.children[0]

    wrapper.addEventListener('scroll', () => {
      let registers = this.registers
      let memory = this.memory
      let range = this.memoryRange
      let showed = memory.slice(...range)
      let f1Value = registers ? registers.f1 : -1

      let scrollTop = wrapper.scrollTop
      let height = wrapper.clientHeight
      let lineHeight = 33

      let html = ''

      let startIndex = Math.floor(scrollTop / lineHeight)
      let endIndex = Math.floor((scrollTop + height) / lineHeight)

      for (let i = startIndex; i <= endIndex; i++) {
        let value = showed[i]
        let top = i * lineHeight
        let bottom = top + lineHeight
        if (bottom > scrollTop && top < scrollTop + height) {
          html += `
          <div style="top:${top}px; ${i === f1Value ? 'background: rgba(33, 136, 255, 0.2);' : ''}">
            <div class="index-name">${Number(i) + range[0]}</div>
            <span>${value}</span>
          </div>
          `
        }
      }

      content.innerHTML = html
    })
  }

  updateMemory() {
    let { memory, registers } = this
    this.memory = memory
    let f1Value = registers ? registers.f1 : -1

    if (this.activeTabKey !== 'memory') {
      return
    }

    let beforeRange = this.beforeMemoryRange
    let range = this.memoryRange
    let showed = memory.slice(...range)

    let wrapper = document.querySelector('.display-stat-tab-pane .display-memory-content-wrapper')
    let content = wrapper.children[0]
    let scrollTop = wrapper.scrollTop
    let height = wrapper.clientHeight
    let lineHeight = 33

    let beforeAmount = beforeRange[1] - beforeRange[0]
    let currentAmount = range[1] - range[0]
    if (currentAmount !== beforeAmount) {
      content.style.height = `${currentAmount * lineHeight}px`
    }

    let html = ''

    let startIndex = Math.floor(scrollTop / lineHeight)
    let endIndex = Math.floor((scrollTop + height) / lineHeight)


    for (let i = startIndex; i <= endIndex; i++) {
      let memoryIndex = Number(i) + range[0]
      let value = showed[i]
      let top = i * lineHeight
      let bottom = top + lineHeight
      if (bottom > scrollTop && top < scrollTop + height) {
        html += `
        <div style="top:${top}px; ${memoryIndex === f1Value ? 'background: rgba(33, 136, 255, 0.2);' : ''}">
          <div class="index-name">${memoryIndex}</div>
          <span>${value}</span>
        </div>
        `
      }
    }

    content.innerHTML = html
  }

  update(info) {
    let { registers, memory } = info
    this.registers = registers
    this.memory = memory
    this.updateRegister()
    this.updateMemory()
  }
}

export default Display
