// 'pa': 0b00000000,
// 'a1': 0b00010000,
// 'a2': 0b00100000,
// 'a3': 0b00110000,
// 'c1': 0b01000000,

const number_from_lower_edian = function (v1: number, v2: number) {
  const r = (v2 << 8) + v1
  return r
}

const number_to_lower_edian = function (v: number | string) {
  if (typeof v === 'string') {
    v = parseInt(v)
  }
  // 16 位
  const n = Math.floor(v)
  const n1 = (n >> 8) & 255
  const n2 = n & 255
  return [n2, n1]
}

export class AxePU {
  registers: {
    [key: string]: number
  }
  memory: number[]
  onEachExcute: (cpu: AxePU) => void
  onEnd: () => void
  shouldPause: (cpu: AxePU) => boolean
  screenMemoryRange: [number, number]
  paused: boolean

  constructor({
    memory,
    screenMemoryRange,
    onEachExcute,
    shouldPause,
    onEnd,
  }: {
    memory: number[];
    screenMemoryRange: [number, number],
    onEachExcute(cpu: AxePU): void;
    shouldPause: (cpu: AxePU) => boolean;
    onEnd: () => void
  }) {
    this.memory = memory
    this.screenMemoryRange = screenMemoryRange
    this.onEachExcute = onEachExcute
    this.shouldPause = shouldPause
    this.paused = false
    this.onEnd = onEnd

    this.registers = {
      a1: 0,
      a2: 0,
      a3: 0,
      c1: 0,
      pa: 0,
      f1: 0,
    }
  }

  pa() {
    return this.registers['0']
  }

  set_register(register: number, value: number) {
    const r = register
    if (r === 0) {
      this.registers['pa'] = value
    } else if (r === 16) {
      this.registers['a1'] = value
    } else if (r === 32) {
      this.registers['a2'] = value
    } else if (r === 48) {
      this.registers['a3'] = value
    } else if (r === 80) {
      this.registers['f1'] = value
    }
  }

  register(register: number) {
    const r = register
    if (r === 0) {
      return this.registers['pa']
    } else if (r === 16) {
      return this.registers['a1']
    } else if (r === 32) {
      return this.registers['a2']
    } else if (r === 48) {
      return this.registers['a3']
    } else if (r === 64) {
      return this.registers['c1']
    } else if (r === 80) {
      return this.registers['f1']
    }

    throw new Error(`无法识别的 register 值：${r}`)
  }

  // 'set': 0b00000000,
  // 'load': 0b00000001,
  // 'add': 0b00000010,
  // 'save': 0b00000011,
  // 'compare': 0b00000100,
  // 'jump_if_great': 0b00000101,
  // 'jump': 0b00000110,
  // 'save_from_register': 0b00000111,
  _run() {
    let m = this.memory
    let pa = this.registers['pa']
    let op = m[pa]

    this.onEachExcute(this)
    if (this.shouldPause(this)) {
      return
    }

    if (op === 0) {
      // 'set'
      let r = m[pa + 1]
      let v = m[pa + 2]
      this.registers['pa'] = pa + 3
      this.set_register(r, v)
    } else if (op === 1) {
      // load
      let a = m[pa + 1]
      let r = m[pa + 2]
      this.registers['pa'] = pa + 3
      this.set_register(r, m[a])
    } else if (op === 3) {
      // save
      let r = m[pa + 1]
      let a = m[pa + 2]
      this.registers['pa'] = pa + 3
      m[a] = this.register(r)
    } else if (op === 2) {
      // add
      let r1 = m[pa + 1]
      let r2 = m[pa + 2]
      let r3 = m[pa + 3]
      this.registers['pa'] = pa + 4

      let v1 = this.register(r1)
      let v2 = this.register(r2)
      this.set_register(r3, v1 + v2)
    } else if (op === 4) {
      // compare
      let r1 = m[pa + 1]
      let r2 = m[pa + 2]
      this.registers['pa'] = pa + 3

      let v1 = this.register(r1)
      let v2 = this.register(r2)

      // 0 表示小于，1 表示相等，2 表示大于
      let c = 0
      if (v1 === v2) {
        c = 1
      } else if (v1 > v2) {
        c = 2
      }
      this.registers['c1'] = c
    } else if (op === 5) {
      // jump_if_less
      let a1 = m[pa + 1]
      let a2 = m[pa + 2]
      let a = number_from_lower_edian(a1, a2)

      this.registers['pa'] = pa + 3

      let c = this.registers['c1']
      // 0 表示小于，1 表示相等，2 表示大于
      if (c === 0) {
        this.registers['pa'] = a
      }
    } else if (op === 6) {
      // jump
      let a1 = m[pa + 1]
      let a2 = m[pa + 2]
      let a = number_from_lower_edian(a1, a2)
      this.registers['pa'] = a
    } else if (op === 7) {
      // save_from_register
      let r = m[pa + 1]
      let a = m[pa + 2]
      this.registers['pa'] = pa + 3

      let address = this.register(a)
      m[address] = this.register(r)
    } else if (op === 8) {
      // set2
      let r = m[pa + 1]
      let v1 = m[pa + 2]
      let v2 = m[pa + 3]
      this.registers['pa'] = pa + 4

      let v = number_from_lower_edian(v1, v2)
      this.set_register(r, v)
    } else if (op === 9) {
      // load2
      let a1 = m[pa + 1]
      let a2 = m[pa + 2]
      let r = m[pa + 3]
      this.registers['pa'] = pa + 4

      let a = number_from_lower_edian(a1, a2)
      this.set_register(r, m[a])
    } else if (op === 10) {
      // add2
      let r1 = m[pa + 1]
      let r2 = m[pa + 2]
      let r3 = m[pa + 3]
      this.registers['pa'] = pa + 4

      let v1 = this.register(r1)
      let v2 = this.register(r2)
      // let v = (v1 + v2) & 65535
      let v = v1 + v2
      this.set_register(r3, v)
    } else if (op === 11) {
      // save2
      let r = m[pa + 1]
      let a1 = m[pa + 2]
      let a2 = m[pa + 3]
      this.registers['pa'] = pa + 4

      let a = number_from_lower_edian(a1, a2)
      if (a === 65534) {
        console.log(this.registers)
      } else {
        m[a] = this.register(r)
      }
    } else if (op === 12) {
      // subtract2
      let r1 = m[pa + 1]
      let r2 = m[pa + 2]
      let r3 = m[pa + 3]
      this.registers['pa'] = pa + 4

      let v1 = this.register(r1)
      let v2 = this.register(r2)
      // let v = (v1 - v2) & 65535
      let v = v1 - v2
      this.set_register(r3, v)
    } else if (op === 13) {
      // load_from_register
      let r1 = m[pa + 1]
      let r2 = m[pa + 2]
      this.registers['pa'] = pa + 3

      let a = this.register(r1)
      let v = m[a]
      this.set_register(r2, v)
    } else if (op === 14) {
      // load_from_register2
      let r1 = m[pa + 1]
      let r2 = m[pa + 2]
      this.registers['pa'] = pa + 3

      let a = this.register(r1)
      let v1 = m[a]
      let v2 = m[a + 1]
      let v = number_from_lower_edian(v1, v2)
      this.set_register(r2, v)
    } else if (op === 15) {
      // save_from_register2
      let r1 = m[pa + 1]
      let r2 = m[pa + 2]
      this.registers['pa'] = pa + 3

      let v = this.register(r1)
      let a = this.register(r2)

      let vs = number_to_lower_edian(v)
      m[a] = vs[0]
      m[a + 1] = vs[1]
    } else if (op === 16) {
      // jump_from_register
      let r1 = m[pa + 1]

      let a = this.register(r1)

      this.registers['pa'] = a
    } else if (op === 17) {
      // shift_right a1 a2 a3
      let r1 = m[pa + 1]
      let r2 = m[pa + 2]
      let r3 = m[pa + 3]
      this.registers['pa'] = pa + 4

      let v1 = this.register(r1)
      let v2 = this.register(r2)
      let v = v1 >> v2
      this.set_register(r3, v)
    } else if (op === 19) {
      // and a1 a2 a3
      let r1 = m[pa + 1]
      let r2 = m[pa + 2]
      let r3 = m[pa + 3]
      this.registers['pa'] = pa + 4

      let v1 = this.register(r1)
      let v2 = this.register(r2)
      let v = v1 & v2
      this.set_register(r3, v)
    } else if (op === 20) {
      // multiply2
      let r1 = m[pa + 1]
      let r2 = m[pa + 2]
      let r3 = m[pa + 3]
      this.registers['pa'] = pa + 4

      let v1 = this.register(r1)
      let v2 = this.register(r2)
      let v = v1 * v2
      this.set_register(r3, v)
    } else if (op === 255) {
      // halt
      this.onEnd && this.onEnd()
      return
    } else {
      throw new Error(`无法识别的 op: ${op}`)
    }

    setTimeout(() => {
      if (!this.paused) {
        this._run()
      }
    }, 0)
  }

  run() {
    this.paused = false
    setTimeout(() => {
      this._run()
    }, 0)
  }

  pause() {
    this.paused = true
  }
}

