import Element from '../../lib/element'

const getColorValue = (bit, offset) => {
  return ((bit >> offset) & 0b11) / 3 * 255
}

let beforeMemory = []

class Screen extends Element {
  pixelSize: number
  context: CanvasRenderingContext2D

  constructor() {
    super()

    this.pixelSize = 500 / 100
    let s: any = document.querySelector('#screen')
    this.context = s.getContext('2d')
  }

  drawPoint(x: number, y: number, color: [number, number, number, number]) {
    let { context, pixelSize } = this
    context.fillStyle = `rgba(${color.toString()})`
    context.fillRect(x * pixelSize, y * pixelSize, pixelSize, pixelSize)
  }

  update(info) {
    let { memory } = info
    let memoryPart = memory.slice(30000, 40000)
    let width = 100
    for (let i = 0; i < memoryPart.length; i++) {
      let bit = memoryPart[i]
      if (beforeMemory[i] !== bit) {
        // 与上一次渲染的值不同时，才更新界面避免浪费性能

        let y = Math.floor(i / width)
        let x = i % width

        let r = getColorValue(bit, 6)
        let g = getColorValue(bit, 4)
        let b = getColorValue(bit, 2)
        let a = getColorValue(bit, 0)
        let color: any = [r, g, b, a]
        this.drawPoint(x, y, color)
      }
    }

    beforeMemory = memoryPart
  }

  clear() {
    let { context, pixelSize } = this
    context.fillStyle = 'rgba(0, 0, 0, 255)'
    context.fillRect(0, 0, 500, 500)
  }
}

export default Screen
