import './index.less'
import { editor as monacoEditor, Range } from 'monaco-editor'
import './languages/asm'
// import debuggerItem from '../debugger'
import Element from '@/lib/element'
import guaA16 from '@/a16Scripts/gua.a16'
// import factorialA16 from '@/a16Scripts/factorial.a16'
import Interpreter from '../../lib/interpreter'
import { debounce } from '../../lib/interpreter/utils'

// const code = guaA16
// const code = factorialA16

const code = `

jump @1024
.memory 1024
set2 f1 3
jump @__main



@function_add a b
  ; 下面的 c 是函数内的局部变量
  .var2 c
  ; c = a + b
  .add2 a b c
  ; 把变量 c 放入寄存器 a1 并返回
  .return c

@__main
  .var2 a 12
  .var2 b 233
  .call @function_add a b

halt
  `

// TODO 这里先写死文件名字，后面改成动态获取
const names = ['index.a16', 'gua_icon.a16']
const models = [
  code,
  guaA16,
].map((item) => monacoEditor.createModel(item, 'asm'))

class IDE extends Element {
  constructor({ onRun, onContinue, onStep, onStop }) {
    super()
    this.beforeLine = undefined
    this.beforeOriginLine = undefined

    this.asmSourceMap = undefined
    this.beforeMap = undefined
    this.currentFile = ''

    this.breakpointMap = {}
    this.onRun = onRun
    this.onContinue = onContinue
    this.onStep = onStep
    this.onStop = onStop

    this.running = false

    this.init()
  }

  get breakpoints() {
    return this.breakpointMap[this.currentFile]
  }

  set breakpoints(value) {
    this.breakpointMap[this.currentFile] = value
  }

  init() {
    this.editor = this.loadEditor()
    this.loadTabs()
    this.loadCode()

    this.bindOperations()
  }

  bindOperations() {
    const { onContinue, onStep } = this

    const buttons = document.querySelectorAll('#code .code-controller-op .code-btn')
    const runBtn = buttons[0]
    const continueBtn = buttons[1]
    const stepBtn = buttons[2]
    const stopBtn = buttons[3]

    this.initStop()

    runBtn.addEventListener('click', () => {
      this.run()
    })
    continueBtn.addEventListener('click', () => {
      if (this.running) {
        onContinue()
      }
    })
    stepBtn.addEventListener('click', () => {
      if (this.running) {
        onStep()
      }
    })
    stopBtn.addEventListener('click', () => {
      this.stop()
    })

    window.addEventListener('keydown', (e) => {
      if (e.ctrlKey) {
        if (e.key === 'r') {
          this.run()
        }
      }
    })
  }

  initStop() {
    const buttons = document.querySelectorAll('#code .code-controller-op .code-btn')
    const runBtn = buttons[0]
    const continueBtn = buttons[1]
    const stepBtn = buttons[2]
    const stopBtn = buttons[3]


    let disabledCls = 'disabled'

    runBtn.classList.remove(disabledCls)
    runBtn.classList.remove('loading')
    continueBtn.classList.add(disabledCls)
    stepBtn.classList.add(disabledCls)
    stopBtn.classList.add(disabledCls)
  }

  stop() {
    const { onStop } = this

    onStop()
    this.running = false
    this.initStop()

    this.editor.updateOptions({
      readOnly: false,
    })
  }

  initRun() {
    const buttons = document.querySelectorAll('#code .code-controller-op .code-btn')
    const runBtn = buttons[0]
    const continueBtn = buttons[1]
    const stepBtn = buttons[2]
    const stopBtn = buttons[3]

    let disabledCls = 'disabled'

    runBtn.classList.add(disabledCls)
    runBtn.classList.add('loading')
    continueBtn.classList.remove(disabledCls)
    stepBtn.classList.remove(disabledCls)
    stopBtn.classList.remove(disabledCls)
  }

  onCompile = (text) => {
    let editor = this.compiledEditor
    editor.setValue(text)
  }

  run() {
    const { onRun, editor, breakpoints } = this

    console.log(this.breakpointMap)

    if (!this.running) {
      const text = editor.getValue()
      onRun({ code: text, breakpoints })
      this.running = true
      this.initRun()

      this.editor.updateOptions({
        readOnly: true,
      })
    }
  }

  loadTabs = () => {
    const tabs = document.querySelector('#code .code-controller-tabs')

    const html = names
      .map((item, index) => {
        return `
          <div class="code-controller-tab ${index === 0 ? 'active' : ''}">
            <span>
              <i class="asm-icon">&#xe60f;</i>
              ${item}
            </span>
            <i class="asm-icon code-controller-tab-close">&#xe615;</i>
          </div>
        `
      }).join('')

    tabs.innerHTML = html

    tabs.addEventListener('click', (e) => {
      let t = e.target
      let elt = t.closest('.code-controller-tab')
      if (elt && !elt.classList.contains('active')) {
        let items = Array.from(tabs.children)
        for (let e of items) {
          e.classList.remove('active')
        }
        elt.classList.add('active')

        let i = items.indexOf(elt)
        let model = models[i]
        let filename = names[i]
        this.currentFile = filename

        let editor = this.editor
        editor.setModel(model)

        let code = model.getValue()
        this.updateCompiled(code)

        this.breakpoints = new Set()
      }
    })
  }

  loadCode = () => {
    let m = models[0]
    this.editor.setModel(m)
    this.currentFile = names[0]

    let code = m.getValue()
    this.updateCompiled(code)
    this.breakpoints = new Set()
  }

  loadEditor = () => {
    let onEditorClick = this.onEditorClick
    const ctns = document.querySelectorAll('#code .code-editor-wrapper > div')
    const ctn = ctns[0]
    const diff = ctns[1]
    const edi = monacoEditor.create(ctn, {
      language: 'asm',
      theme: 'asm-theme',
      minimap: {
        enabled: false,
      },
      fontFamily: 'MonoLisa, Menlo, Monaco, "Courier New", monospace',
      fontSize: 14,
      automaticLayout: true,
      tabSize: 2,
    })

    ctn.addEventListener('click', onEditorClick)

    const compiled = monacoEditor.create(diff, {
      language: 'asm',
      theme: 'asm-theme',
      readOnly: true,
      minimap: {
        enabled: false,
      },
      fontFamily: 'MonoLisa, Menlo, Monaco, "Courier New", monospace',
      fontSize: 14,
      automaticLayout: true,
      tabSize: 2,
    })

    diff.addEventListener('keydown', () => {
      handleMsg('右侧编辑器不可修改')
    })

    this.compiledEditor = compiled

    const onUpdate = debounce((e) => {
      let v = edi.getValue()
      let lineNumber = e.changes[0].range.startLineNumber
      this.updateCompiled(v, lineNumber)
    }, 300)

    edi.onDidChangeModelContent(onUpdate)
    edi.onDidChangeCursorPosition((e) => {
      let lineNumber = e.position.lineNumber
      if (this.asmSourceMap) {
        let map = this.asmSourceMap[lineNumber]
        if (this.beforeMap) {
          let { startLine, endLine } = this.beforeMap
          this.removeCompareBackground(startLine, endLine)
        }

        if (map) {
          this.addCompareBackground(map.startLine, map.endLine)
          compiled.revealLineNearTop(map.startLine)
          this.beforeMap = map
        }
      }
    })

    return edi
  }

  updateCompiled = (v, lineNumber) => {
    let compiled = this.compiledEditor
    let compiledAsm = Interpreter.parseAsm(v)
    let code = compiledAsm.code
    compiled.setValue(code)
    let asmSourceMap = this.asmSourceMap = compiledAsm.sourceMap

    if (lineNumber) {
      let map = asmSourceMap[lineNumber]
      this.addCompareBackground(map.startLine, map.endLine)
      this.beforeMap = map
    }
  }

  addHighlightLine(editor, lineNumber) {
    let className = 'highlight'
    this.addLineClass(editor, {
      startLine: lineNumber,
      endLine: lineNumber,
      className,
    })
  }

  removeHighLightLine(editor, lineNumber) {
    let className = 'highlight'
    this.removeLineClass(editor, {
      startLine: lineNumber,
      endLine: lineNumber,
      className,
    })
  }

  addCompareBackground(startLine, endLine) {
    let editor = this.compiledEditor
    let className = 'compare-bg'
    this.addLineClass(editor, {
      startLine,
      endLine,
      className,
    })
  }

  removeCompareBackground(startLine, endLine) {
    let editor = this.compiledEditor
    let className = 'compare-bg'
    this.removeLineClass(editor, {
      startLine,
      endLine,
      className,
    })
  }

  addLineClass(editor, { startLine, endLine, className }) {
    let model = editor.getModel()
    if (!model) {
      return
    }

    let value = {
      range: new Range(startLine, 1, endLine, 1),
      options: { isWholeLine: true, className },
    }

    model.deltaDecorations([], [value])
  }

  removeLineClass(editor, { startLine, endLine, className }) {
    let model = editor.getModel()
    if (!model) {
      return
    }

    let decorations = []
    for (let i = startLine; i <= endLine; i++) {
      let d = editor.getLineDecorations(i)
      decorations = [...decorations, ...d]
    }

    let ids = []
    for (let decoration of decorations) {
      if (decoration.options.className === className) {
        ids.push(decoration.id)
      }
    }
    if (ids && ids.length) {
      model.deltaDecorations(ids, [])
    }
  }

  addBreakPoint(line) {
    let model = this.editor.getModel()
    if (!model) {
      return
    }

    let value = {
      range: new Range(line, 1, line, 1),
      options: { isWholeLine: true, linesDecorationsClassName: 'breakpoint' },
    }

    model.deltaDecorations([], [value])
  }

  removeBreakPoint (line) {
    let model = this.editor.getModel()
    if (!model) {
      return
    }

    let decorations
    let ids = []
    if (line !== undefined) {
      decorations = this.editor.getLineDecorations(line)
    } else {
      decorations = this.editor.getAllDecorations()
    }
    for (let decoration of decorations) {
      if (decoration.options.linesDecorationsClassName === 'breakpoint') {
        ids.push(decoration.id)
      }
    }
    if (ids && ids.length) {
      model.deltaDecorations(ids, [])
    }
  }

  hasBreakPoint (line) {
    let decorations = this.editor.getLineDecorations(line)
    for (let decoration of decorations) {
      if (decoration.options.linesDecorationsClassName === 'breakpoint') {
        return true
      }
    }
    return false
  }

  onEditorClick = (e) => {
    const { target } = e
    let a = target
    let breakpoints = this.breakpoints

    if (!a.classList.contains('line-numbers') || !a.closest('.margin-view-overlays')) {
      return
    }

    const getLineCount = () => {
      return parseInt(a.innerText)
    }

    // let cls = 'selected'
    let count = getLineCount()
    if (this.hasBreakPoint(count)) {
      this.removeBreakPoint(count)
      // a.classList.remove(cls)
      breakpoints.delete(count)
      // onBreakpointRemove(count)
    } else {
      this.addBreakPoint(count)
      // a.classList.add(cls)
      breakpoints.add(count)
      // onBreakpointAdd(count)
    }
  }

  update(info) {
    const updateHighlight = () => {
      let n = info.line
      let originLine = info.originLine

      let editor = this.compiledEditor
      editor.revealLineNearTop(n)

      let beforeLine = this.beforeLine
      if (beforeLine) {
        this.removeHighLightLine(editor, beforeLine)
      }
      this.addHighlightLine(editor, n)
      this.beforeLine = n

      if (originLine !== this.beforeOriginLine) {
        let editor = this.editor
        this.addHighlightLine(editor, originLine)
        // editor.revealLineNearTop(originLine)

        if (this.beforeOriginLine) {
          this.removeHighLightLine(editor, this.beforeOriginLine)
        }

        this.beforeOriginLine = originLine
      }
    }

    updateHighlight()
  }
}

export default IDE
