import parsePseudo from './parsePseudo'
import parseBasic from './parseBasic'

export const machine_code = function (raw: string) {
  let compiledAsm = parsePseudo(raw)
  let asm: string = compiledAsm.code
  let res = parseBasic(asm)

  let lineMap: {
    [key: number]: number;
  } = {}

  for (let [k, v] of Object.entries(compiledAsm.sourceMap)) {
    let line = Number(k)
    let { startLine, endLine } = v
    for (let i = startLine; i <= endLine; i++) {
      lineMap[i] = line
    }
  }

  return {
    ...res,
    lineMap,
  }
}
