import './index.less'
import Modal from '../modal'
import Tabs from '../tabs'

import introMemory from './md/memory.md'
import introKeyBind from './md/keybind.md'
import introInstruction from './md/instruction.md'
import introPseudo from './md/pseudo.md'
import breakpointImg from './img/breakpoint.png'

const numberTo2bit = (a) => {
  let res = '0b' + a.toString(2).padStart(8, '0')
  return res
}

const splitInstructionIntro = (text) => {
  let regex = /<p (\w+) (\d+)>/ig
  let match = text.match(regex)
  let indices = match.map((i) => text.indexOf(i))

  let parts = []

  for (let i = 0; i < indices.length; i++) {
    let index = indices[i]
    let m = match[i]
    let ps = m.slice(1, m.length - 1).split(' ')
    let inst = ps[1]
    let code = Number(ps[2])

    let p = `
    <h1>${inst}</h1>
    <h2>机器码</h2>
    <ul>
      <li>
        二进制：
        <code>${numberTo2bit(code)}</code>
      </li>
      <li>
        十进制：
        <code>${code}</code>
      </li>
    </ul>
    <h2>使用方式</h2>
    ${text.slice(index + m.length, indices[i + 1])}
    `

    parts.push({
      title: inst,
      content: p,
    })
  }

  return parts
}

const instructionIntros = splitInstructionIntro(introInstruction)


const bindModal = () => {
  const guideModal = new Modal('.header .header-right .asm-modal')
  const guideButton = document.querySelector('.header .header-right .header-btn')

  guideButton.addEventListener('click', () => {
    guideModal.show()
  })
  // guideModal.show()

}

const bindTabs = () => {
  let sel = '.header .header-right .header-guide-tabs'
  let tabs = new Tabs(sel, {
    options: [
      {
        title: '<i class="asm-icon">&#xe629;</i>内存方案',
        content: introMemory,
      },
      {
        title: '<i class="asm-icon">&#xe64d;</i>键盘绑定',
        content: introKeyBind,
      },
      {
        title: '<i class="asm-icon">&#xe837;</i>指令说明',
        content: '汇编中指令的说明',
        children: instructionIntros,
      },
      {
        title: '<i class="asm-icon">&#xe837;</i>伪指令说明',
        content: introPseudo,
      },
      {
        title: '<i class="asm-icon">&#xe837;</i>断点功能',
        content: `
          <img src="${breakpointImg}"/>
        `,
      },
    ],
  })
  return tabs

}

const guide = () => {
  bindModal()
  bindTabs()
}

export default guide
