import { machine_code } from './asm'
import { AxePU } from './vm'
import parsePseudo from './asm/parsePseudo'

const getBreakPaValues = (sourcemap: any, linemap: any, breakpoints: Set<number>) => {
  let paSet = new Set<number>()
  let addedLine = new Set<number>()
  // @ts-ignore
  for (let [pa, { line }] of Object.entries(sourcemap)) {
    let actualLine = linemap[line]
    if (breakpoints.has(actualLine) && !addedLine.has(actualLine)) {
      addedLine.add(actualLine)
      paSet.add(parseInt(pa))
    }
  }

  return paSet
}

class Interpreter {
  cpu: AxePU
  running: boolean
  stepRun = false

  static parseAsm(asm: string) {
    let code = parsePseudo(asm, true)
    return code
  }

  constructor(code: string, options: any) {
    this.cpu = this.initCpu(code, options)
    this.running = false
  }

  initCpu(code: string, options: any) {
    const { onEachExecute, breakpoints, onEnd } = options
    const res: any = machine_code(code)
    const { memory, sourcemap, lineMap } = res
    // console.log(memory)
    for (let i = 0; i < 65535; i += 1) {
      memory.push(0)
    }

    // memory[30000] = 0b11000011
    // memory[30001] = 0b00110011
    // memory[30002] = 0b00001111
    // memory[30100] = 0b11001111

    console.log(sourcemap)

    const paValues = getBreakPaValues(sourcemap, lineMap, breakpoints)

    const onExecute = (cpu: AxePU) => {
      const { pa } = cpu.registers
      const sm = sourcemap[pa]
      const line = sm.line
      const originLine = lineMap[line]

      const info = {
        line,
        originLine,
        registers: cpu.registers,
        memory,
      }
      onEachExecute(info)
    }

    let beforePa = -1

    const shouldPause = (cpu: AxePU) => {
      let { stepRun } = this
      const { pa } = cpu.registers
      if (stepRun && pa !== beforePa) {
        beforePa = pa
        return true
      }

      if (paValues.has(pa) && pa !== beforePa) {
        beforePa = pa
        return true
      }
      return false
    }

    let cpu = new AxePU({
      memory,
      onEachExcute: onExecute,
      shouldPause,
      screenMemoryRange: [30000, 39999],
      onEnd,
    })

    return cpu
  }

  run() {
    if (!this.running) {
      this.running = true
      this.cpu.run()

      console.log(this.cpu.registers)
    }
  }

  continue() {
    let { cpu } = this
    this.stepRun = false
    cpu.run()
  }

  next() {
    let { cpu } = this
    this.stepRun = true
    cpu.run()
  }

  stop() {
    let { cpu } = this
    cpu.pause()
  }
}

export default Interpreter
