import './index.less'
import IDE from '../ide'
import Display from '../diaplay'
import Interpreter from '@/lib/interpreter'
import Screen from '../screen'
import header from '@/components/header'

let dummyInterpreter = new Interpreter('halt', {
  onEachExecute: (info) => {
    this.update(info)
  },
  breakpoints: new Set(),
})

class App {
  constructor() {
    header()

    this.elements = []

    this.init()
  }

  init() {
    let screen = new Screen()

    let runner = new Interpreter('halt', {
      onEachExecute: (info) => {
        this.update(info)
      },
      breakpoints: new Set(),
    })

    const onRun = ({ code, breakpoints }) => {
      screen.clear()

      runner = new Interpreter(code, {
        breakpoints,
        onEachExecute: (info) => {

          this.update(info)
        },
        onEnd() {
          ide.stop()
        },
      })

      runner.run()
    }

    const onContinue = () => {
      runner.continue()
    }

    const onStep = () => {
      runner.next()
    }

    const onStop = () => {
      runner.stop()
    }

    let ide = new IDE({ onRun, onContinue, onStep, onStop })
    let display = new Display()

    this.pushElement(ide)
    this.pushElement(display)
    this.pushElement(screen)

    runner.run()

    this.initFooter()
  }

  initFooter() {
    let footer = document.querySelector('.app-footer')
    let ideVersion = process.env.version
    let asmVersion = process.env.asmVersion

    footer.innerHTML = `
    <div class="app-version">version: ${ideVersion}</div>
    <div class="app-ide-info">
      <div>Spaces: 2</div>
      <div>LF</div>
      <div>a16</div>
      <div>${asmVersion}</div>
    </div>
    `
  }

  pushElement(elt) {
    this.elements.push(elt)
  }

  update(info) {
    // console.log(info)
    for (let element of this.elements) {
      element.update(info)
    }
  }
}

const app = () => {
  new App()
}

export default app
