import './index.less'

class RangeSlider {
  constructor(selector, {
    min,
    max,
    step,
    value,
    onChange,
  }) {

    this.container = document.querySelector(selector)
    this.value = value
    this.options = {
      min,
      max,
      step,
      onChange,
    }

    this.init()
  }

  init() {
    this.initContainer()
    this.initSlider()
  }

  initContainer() {
    const container = this.container
    const { min, max } = this.options

    container.innerHTML = `
    <div class="asm-range-slider">
      <div class="asm-range-slider-button">
        <div class="asm-range-slider-range-value"></div>
      </div>
      <div class="asm-range-slider-rail"></div>
      <div class="asm-range-slider-button">
        <div class="asm-range-slider-range-value"></div>
      </div>
      <div class="asm-range-slider-min">${min}</div>
      <div class="asm-range-slider-max">${max}</div>
    </div>
    `
  }

  initSlider() {
    const { container, options, value } = this
    const {
      min,
      max,
      step,
      onChange,
    } = options

    const slider = container.querySelector('.asm-range-slider')
    const elements = slider.children
    const button1 = elements[0]
    const button2 = elements[2]

    let startX = 0
    let startLeft = 0
    let sliderWidth = slider.clientWidth
    let maxLeft = sliderWidth - 10
    let pixelStep = sliderWidth / (max - min) * step

    let range = [value[0], sliderWidth / (max - min) * value[1]]
    let clickedElement = null

    this.update(range)

    let getPosition = (elt) => {
      let left = (parseInt(elt.style.left) || -10) + 10
      return left
    }

    const onButtonClick = (e) => {
      clickedElement = e.target
      startX = e.pageX
      startLeft = getPosition(clickedElement)
    }

    button1.addEventListener('mousedown', onButtonClick)
    button2.addEventListener('mousedown', onButtonClick)

    window.addEventListener('mousemove', (e) => {
      let elt = clickedElement
      if (elt) {
        let offset = e.pageX - startX
        // 根据 step 计算实际 offset
        offset = Math.floor(offset / pixelStep) * pixelStep

        let newLeft = startLeft + offset
        let width = range[1] - range[0]

        if (elt === button1) {
          if (newLeft < 0) {
            newLeft = 0
          } else if (newLeft > maxLeft - width) {
            newLeft = maxLeft - width
          }
        } else {
          if (newLeft < width) {
            newLeft = width
          } else if (newLeft > maxLeft) {
            newLeft = maxLeft
          }
        }

        let newRange = [...range]
        if (elt === button1) {
          newRange = [newLeft, newLeft + width]
        } else {
          newRange = [newLeft - width, newLeft]
        }

        if (newRange[0] !== range[0]) {
          range = newRange
          this.update(newRange)
        }
      }
    })

    window.addEventListener('mouseup', () => {

      if (clickedElement) {
        if (onChange) {
          onChange(this.value)
        }
        clickedElement = false
      }
    })
  }

  update(range) {
    const { container, options, value } = this
    const {
      min,
      max,
      step,
      onChange,
    } = options

    const slider = container.querySelector('.asm-range-slider')
    const elements = slider.children
    const button1 = elements[0]
    const button2 = elements[2]
    const rail = elements[1]
    let stepRange = value[1] - value[0]

    const updateRail = (range) => {
      rail.style.left = `${Math.min(...range)}px`
      rail.style.width = `${Math.abs(range[0] - range[1])}px`
    }

    const updateButton = (range) => {
      button1.style.left = `${range[0] - 10}px`
      button2.style.left = `${range[1] - 10}px`
    }

    let sliderWidth = slider.clientWidth
    let maxLeft = sliderWidth - 10
    let pixelStep = sliderWidth / (max - min) * step

    const updateValue = (range) => {
      let [start, end] = range
      let value
      if (end === maxLeft) {
        value = [max - stepRange, max]
      } else {
        let movedSteps = Math.round(start / pixelStep)
        let startValue = movedSteps * step
        value = [startValue, startValue + stepRange]
      }

      button1.children[0].innerText = value[0]
      button2.children[0].innerText = value[1]

      this.value = value
    }

    updateButton(range)
    updateRail(range)
    updateValue(range)
  }
}

export default RangeSlider
