import './index.less'

class Modal {
  constructor(selector) {
    let modal = document.querySelector(selector)
    this.modal = modal

    if (!modal) {
      console.error(`[modal] 没有找到 ${selector} 元素`)
    }

    const mask = modal.querySelector('.asm-modal-mask')
    if (mask) {
      mask.addEventListener('click', this.hide)
    }

    const closeButton = modal.querySelector('.asm-modal-close')
    if (closeButton) {
      closeButton.addEventListener('click', this.hide)
    }
  }

  show = () => {
    let modal = this.modal
    modal.classList.add('asm-modal-active')
  }

  hide = () => {
    let modal = this.modal
    modal.classList.remove('asm-modal-active')
  }
}

export default Modal
