import {
  register_mapper,
  instruction_mapper,
} from '../const'
import { stringIsDigit } from '../utils'

export const number_to_lower_edian = function (v: number | string) {
  // 16 位
  if (typeof v === 'string') {
    v = parseInt(v)
  }
  let n = Math.floor(v)
  let n1 = (n >> 8) & 255
  let n2 = n & 255
  return [n2, n1]
}


const parseBasic = (asm: string) => {

  let registers: any = register_mapper()
  let insts: any = instruction_mapper()

  let lines = asm.split('\n')
  let i = 0
  let result: (number | string)[] = []
  // 用来记录过程中碰到的 label 位置
  let labels: {
    [key: string]: number
  } = {}

  let sourcemap: {
    [key: number]: {
      source: string;
      line: number;
    }
  } = {}

  // 处理常规内容
  while (i < lines.length) {
    let l = lines[i]
    i = i + 1
    l = l.trim()

    if (l) {
      sourcemap[result.length] = {
        source: l,
        line: i,
      }
    }

    let parts = l.split(' ')
    let op = parts[0]
    let code: typeof result = []

    if (op.startsWith('@')) {
      let label = op.substring(1)
      labels[label] = result.length
    } else if (op === '.memory') {
      let n = parseInt(parts[1])
      let count = n - result.length
      for (let k = 0;  k < count; k += 1) {
        code.push(0)
      }

    } else if (op === 'set2') {
      let r = parts[1]
      let v = parts[2]

      let rbit = registers[r]
      let ns = number_to_lower_edian(v)

      code.push(insts[op])
      code.push(rbit)
      code.push(ns[0])
      code.push(ns[1])
    } else if (op === 'load2') {
      let p1 = parts[1]
      let p2 = parts[2]

      let address = parseInt(p1.substring(1))
      let ns = number_to_lower_edian(address)

      code.push(insts[op])
      code.push(ns[0])
      code.push(ns[1])
      code.push(registers[p2])
    } else if (op === 'save2') {
      let p1 = parts[1]
      let p2 = parts[2]
      let address = parseInt(p2.substring(1))
      let ns = number_to_lower_edian(address)

      code.push(insts[op])
      if (address === 65534) {
        code.push(parseInt(p1))
      } else {
        code.push(registers[p1])
      }
      code.push(ns[0])
      code.push(ns[1])
    } else if (
      op === 'load_from_register' ||
      op === 'save_from_register' ||
      op === 'load_from_register2' ||
      op === 'save_from_register2'
    ) {
      let r1 = parts[1]
      let r2 = parts[2]

      code.push(insts[op])
      code.push(registers[r1])
      code.push(registers[r2])
    } else if (op === 'jump_from_register') {
      let r1 = parts[1]

      code.push(insts[op])
      code.push(registers[r1])
    } else if (op === 'set') {
      code.push(insts[op])
      let r = parts[1]
      let n = parts[2]

      let rbit = registers[r]
      let nbit = parseInt(n)
      code.push(rbit)
      code.push(nbit)
    } else if (
      op === 'add' ||
      op === 'add2' ||
      op === 'subtract2' ||
      op === 'multiply2' ||
      op === 'and' ||
      op === 'shift_right'
    ) {
      let r1 = parts[1]
      let r2 = parts[2]
      let r3 = parts[3]

      code.push(insts[op])
      code.push(registers[r1])
      code.push(registers[r2])
      code.push(registers[r3])
    } else if (op === 'save') {
      code.push(insts[op])
      let p1 = parts[1]
      let p2 = parts[2]

      code.push(registers[p1])

      let address = parseInt(p2.substring(1))
      code.push(address)
    } else if (op === 'load') {
      code.push(insts[op])
      let p1 = parts[1]
      let p2 = parts[2]

      let address = parseInt(p1.substring(1))
      code.push(address)

      code.push(registers[p2])
    } else if (op === 'compare') {
      code.push(insts[op])
      let p1 = parts[1]
      let p2 = parts[2]

      code.push(registers[p1])
      code.push(registers[p2])
    } else if (op === 'jump_if_great' || op === 'jump_if_less') {
      code.push(insts[op])
      let p1 = parts[1]

      let label = p1.substring(1)
      // 先加到 code 里面之后第二遍循环再做处理
      code.push(label)
      code.push('')
    } else if (op === 'jump') {
      code.push(insts[op])
      let arg = parts[1].substring(1)

      if (stringIsDigit(arg)) {
        let vs = number_to_lower_edian(parseInt(arg))
        code.push(vs[0])
        code.push(vs[1])
      } else {
        // 加两个，后面方便插入 16 位的数字
        code.push(arg)
        code.push('')
      }
    } else if (op === 'halt') {
      code.push(insts[op])
    } else if (l.length > 0 && l[0] !== ';') {
      const message = `无法识别的字符: ${op}\n命令: ${l}`
      throw new Error(message)
    }

    result = result.concat(code)
  }

  for (let i = 0; i < result.length;  i += 1) {
    let p = result[i]

    if (typeof p === 'string') {
      // 说明是 label
      // label 多留了一个空位，这里需要设置两个位置
      if (labels[p] !== undefined) {
        let vs = number_to_lower_edian(labels[p])
        result[i] = vs[0]
        result[i + 1] = vs[1]
      }
    }
  }

  return {
    memory: result,
    sourcemap,
    compiled: asm,
  }
}

export default parseBasic
