import { editor, languages } from 'monaco-editor'

languages.register({ id: 'asm' })

const monarchObj = {
  tokenPostfix: '.a16',
  operators: [
    'set',
    'save',
    'load',
    'save_from_register',
    'load_from_register',
    'add',
    'subtract',

    'set2',
    'save2',
    'load2',
    'save_from_register2',
    'load_from_register2',
    'add2',
    'subtract2',
    'multiply2',

    'compare',
    'jump_if_less',
    'jump',
    'jump_from_register',

    'shift_right',
    'and',

    'halt',
  ],
  keywords: [
    'a1',
    'a2',
    'a3',
    'f1',
    'c1',
    'pa',
  ],
  tokenizer: {
    root: [
      { include: 'common' },
    ],
    common: [
      [
        /\.(call|memory|var2|add2|return|multiply2|subtract2)/,
        'operators',
      ],
      [
        /[a-z_$][\w$]*/,
        {
          cases: {
            '@operators': 'operators',
            '@keywords': 'keyword',
            '@default': 'identifier',
          },
        },
      ],
      [/\d+/, 'number'],
      [/@\S+/, 'label'],
      [/;.*/, 'comment'],
    ],
  },
}

languages.setMonarchTokensProvider('asm', monarchObj)

editor.defineTheme('asm-theme', {
  base: 'vs',
  inherit: true,
  rules: [
    { foreground: '#d73a49', token: 'operators' },
    { foreground: '#d73a49', token: 'keyword' },
    { foreground: '#005cc5', token: 'number' },
    { foreground: '#6f42c1', token: 'label' },
    { foreground: '#aaaaaa', token: 'comment' },
  ],
  colors: {
    'editor.background': '#fff',
    'editor.lineHighlightBorder': '#eee',
    'editor.lineHighlightBackground': '#eee',
    'editorLineNumber.foreground': '#c2c2c2',
  },
})
