import { reallocateIndent } from '../utils'
import parseFunction from './parseFunction'

export interface Scope {
  name: string;
  variables: string[];
  parent?: string;
}

const createScope = function (name: string) {
  let scope: Scope = {
    name,
    variables: [],
  }

  return scope
}

const parseScopes = function (code: string) {
  let lines = code.split('\n')
  let scopes: {
    [key: string]: Scope;
  } = {}
  let scopeStack: string[] = ['global']
  scopes.global = createScope('global')

  let functionLabels = []

  // 先遍历一遍，找到 function 的 label
  // 这里通过 label 开头是 function 来判定是函数
  // 其他 label 当做不是 function，直接忽略
  for (let l of lines) {
    let parts = l.trim().split(' ')
    let op = parts[0]

    if (op.startsWith('@function')) {
      let label = parts[0]
      functionLabels.push(label)

      scopes[label] = createScope(label)
    }
  }

  // 遍历找到各个作用域的变量
  for (let i = 0; i < lines.length; i++) {
    let l = lines[i]
    let parts = l.trim().split(' ')


    let op = parts[0]

    if (op.startsWith('@')) {
      if (functionLabels.includes(op)) {
        scopeStack.push(op)
      }
    } else if (op === '.return') {
      scopeStack.pop()
    } else if (op === '.var2') {
      // 变量声明，记录在 scope 中

      let sl = scopeStack.length
      let currentScopeName = scopeStack[sl - 1]
      let s = scopes[currentScopeName]

      let variableName = parts[1]
      s.variables.push(variableName)
    }
  }

  return scopes
}

const flattenLineSourceMap = function (sourceMap: {
  [key: number]: {
    startLine: number;
    endLine: number;
  }
}) {
  let map: {
    [key: number]: number;
  } = {}

  for (let [k, v] of Object.entries(sourceMap)) {
    let line = Number(k)
    for (let i = v.startLine; i <= v.endLine; i++) {
      map[i] = line
    }
  }

  return map
}

const parsePseudo = function (raw: string, ingoreError = false) {
  let {
    code,
    sourceMap: _functionSourceMap,
  } = parseFunction(raw)
  let functionSourceMap = flattenLineSourceMap(_functionSourceMap)
  raw = code
  let scopeMap = parseScopes(raw)
  // console.log(scopeMap)

  let lines = raw.split('\n')
  let sourceMap: {
    [key: number]: {
      startLine: number;
      endLine: number;
    }
  } = {}
  let compiledLines = []
  let scopes = [scopeMap.global]

  const getScope = () => {
    let sl = scopes.length
    let scope = scopes[sl - 1]
    return scope
  }

  const loadVar = (name, registerName, scope) => {
    if (isNaN(Number(name))) {
      let vs = scope.variables
      let vn = vs.length
      let offset = vn - vs.lastIndexOf(name)

      let bits = 2
      let pos = offset * bits

      let compiled = `; 读取变量 ${name} 到 ${registerName}
      ; 读取的地址是 f1 - ${pos}
      set2 a3 ${pos}
      subtract2 f1 a3 a3
      load_from_register2 a3 ${registerName}
      `

      return compiled
    }
    let code = `set ${registerName} ${name}`
    return code
  }

  const saveVar = (name, registerName, scope) => {

    let vs = scope.variables
    let vn = vs.length
    let offset = vn - vs.lastIndexOf(name)

    let bits = 2
    let pos = offset * bits

    let compiled = `; 把寄存器 ${registerName} 的值保存到变量 ${name}
    ; 保存的地址是 f1 - ${pos}
    set2 a3 ${pos}
    subtract2 f1 a3 a3
    save_from_register2 ${registerName} a3
    `

    return compiled

  }

  const getActualLineNumber = (lineNumber: number) => {
    let res = functionSourceMap[lineNumber]
    return res
  }

  const parseLine = (l: string, index: number) => {
    l = l.trim()
    let parts = l.split(' ')
    let op = parts[0]

    const addCompiledParts = (p: string) => {
      let lines = p.split('\n')
      let startLine = compiledLines.length + 1
      let endLine = startLine + lines.length - 1
      let lineNumber = getActualLineNumber(index + 1)

      let oldMap = sourceMap[lineNumber]
      if (oldMap) {
        let newMap = {
          startLine: oldMap.startLine,
          endLine,
        }
        sourceMap[lineNumber] = newMap
      } else {
        sourceMap[lineNumber] = {
          startLine,
          endLine,
        }
      }

      for (let l of lines) {
        compiledLines.push(l)
      }
    }

    if (op.startsWith('@')) {
      // 是标签，看看有没有作用域可以更新
      let s = scopeMap[op]
      if (s) {
        scopes.push(s)
      }

      addCompiledParts(l)

    } else if (op === '.call') {
      // .call @function arg1 arg2 ...
      let label = parts[1]
      let args = parts.slice(2)
      let scope = getScope()

      let loadArguments = ''
      for (let i = 0; i < args.length; i++) {
        let arg = args[i]
        let f1Offset = (i + 1) * 2

        loadArguments += `
        ${loadVar(arg, 'a1', scope)}
        ; 把 a1 的值存到内存中去，也就是 f1 + ${f1Offset} 的位置
        set2 a3 ${f1Offset}
        add2 f1 a3 a3
        save_from_register2 a1 a3
        `
      }

      let compiled = `
        ${loadArguments}
        ; 存储 return 返回的 pa 值
        set2 a3 14
        add2 pa a3 a3
        save_from_register2 a3 f1     ; 这行指令占 3 字节

        ;  保存后，要把 f1 的值 +2
        set2 a3 2                     ; 这行指令占 4 字节
        add2 f1 a3 f1                 ; 这行指令占 4 字节
        ;  跳转到函数
        jump ${label}       ; 这行指令占 3 字节
      `

      addCompiledParts(compiled)

    } else if (op === '.return') {
      let p = parts[1]
      let s = scopes.pop()
      let vs = s.variables
      let bitUnit = 2
      let localOffset = bitUnit * vs.length

      if (p && isNaN(Number(p))) {
        // 是变量
        let id = p

        let compiled = `
        ${loadVar(id, 'a1', s)}

        ; .return ${localOffset}
        set2 a3 ${localOffset + 2}
        subtract2 f1 a3 f1
        load_from_register2 f1 a2
        jump_from_register a2
        `

        addCompiledParts(compiled)

      } else {
        // 是数字
        let len = Number(parts[1] || 0)
        let compiled = `
        set2 a3 ${len + 2}
        subtract2 f1 a3 f1
        load_from_register2 f1 a2
        jump_from_register a2
        `

        addCompiledParts(compiled)
      }

    } else if (op === '.var2') {
      let compiled = ''
      let varName = [parts[1]]

      if (parts.length > 2) {
        // TODO 这里的 parts[2] 可能是表达式/变量
        let v = parseInt(parts[2])
        compiled += `
        ; ${varName} = ${v}
        set2 a1 ${v}
        save_from_register2 a1 f1
        `
      } else {
        compiled = `; 初始化 ${varName}, 不赋值`
      }

      compiled += `
      set2 a3 2
      add2 f1 a3 f1
      `

      addCompiledParts(compiled)

    } else if (op === '.add2') {
      let id1 = parts[1]
      let id2 = parts[2]
      let id3 = parts[3]

      let scope = getScope()

      let compiled = `
        ${loadVar(id1, 'a1', scope)}
        ${loadVar(id2, 'a2', scope)}

        ; ${id3} = ${id1} + ${id2}
        ; 计算 ${id1} + ${id2} 并存入 a1 寄存器
        add2 a1 a2 a1
        ${saveVar(id3, 'a1', scope)}
        `

      addCompiledParts(compiled)
    } else if (op === '.multiply2') {
      let id1 = parts[1]
      let id2 = parts[2]
      let id3 = parts[3]

      let scope = getScope()

      let compiled = `
        ${loadVar(id1, 'a1', scope)}
        ${loadVar(id2, 'a2', scope)}

        ; ${id3} = ${id1} * ${id2}
        ; 计算 ${id1} * ${id2} 并存入 a1 寄存器
        multiply2 a1 a2 a1
        ${saveVar(id3, 'a1', scope)}
        `

      addCompiledParts(compiled)
    } else if (op === '.subtract2') {
      let id1 = parts[1]
      let id2 = parts[2]
      let id3 = parts[3]

      let scope = getScope()

      let compiled = `
        ${loadVar(id1, 'a1', scope)}
        ${loadVar(id2, 'a2', scope)}

        ; ${id3} = ${id1} - ${id2}
        ; 计算 ${id1} - ${id2} 并存入 a1 寄存器
        subtract2 a1 a2 a1
        ${saveVar(id3, 'a1', scope)}
        `

      addCompiledParts(compiled)
    } else {
      addCompiledParts(l)
    }
  }

  for (let i = 0; i < lines.length; i++) {
    let l = lines[i]
    try {
      parseLine(l, i)
    } catch (error) {
      if (ingoreError) {
        console.error(error)
        compiledLines.push(l)
      } else {
        throw error
      }
    }
  }

  let res = reallocateIndent(compiledLines)

  return {
    code: res,
    sourceMap,
  }
}

export default parsePseudo
