import { reallocateIndent } from '../utils'

const parseFunction = (asm: string) => {
  let lines = asm.split('\n')

  let res: string[] = []
  let sourceMap: {
    [key: number]: {
      startLine: number;
      endLine: number;
    }
  } = {}

  const addCompiledParts = (i: number, p: string) => {
    let lines = p.split('\n')
    let startLine = res.length + 1
    let endLine = startLine + lines.length - 1
    sourceMap[i + 1] = {
      startLine,
      endLine,
    }

    for (let l of lines) {
      res.push(l)
    }
  }

  for (let i = 0; i < lines.length; i++) {
    let l = lines[i]
    let parts = l.split(' ')
    let op = parts[0]

    if (op.startsWith('@function')) {
      let args = parts.slice(1)
      let varInit = args
        .map((item) => `.var2 ${item}`)
        .join('\n')

      let code = `${op}\n`

      if (varInit) {
        code += `; 下面的变量 ${args.toString()} 是参数
        ; 由调用者填充具体的值
        ${varInit}`
      }

      addCompiledParts(i, code)

    } else {
      addCompiledParts(i, l)
    }
  }

  let output = reallocateIndent(res)
  return {
    code: output,
    sourceMap,
  }
}

export default parseFunction
