import './index.less'

class Tabs {
  constructor(selector, options) {
    this.element = document.querySelector(selector)
    this.options = options
    this.init()
  }

  init() {
    this.initContent()
    this.bindEvents()
  }

  initContent() {
    let { element } = this
    let { options, className = '' } = this.options

    let titlePart = ''
    let bodyPart = ''
    for (let i = 0; i < options.length; i++) {
      let { title, content, children = [] } = options[i]
      let hasChildren = children.length > 0
      if (hasChildren) {
        let childrenPart = children
          .map((item, j) => {
            return `
            <div class="asm-tabs-tab-item child" data-key="${i}-${j}">
              ${item.title}
            </div>
          `
          })
          .join('')
        titlePart += `
        <div class="asm-tabs-tab-group">
          <div class="asm-tabs-tab-item ${hasChildren ? 'group' : ''}" data-key="${i}">
            ${title}
            <i class="asm-icon expand">&#xe618;</i>
          </div>
          ${childrenPart}
        </div>
        `
      } else {
        titlePart += `
        <div class="asm-tabs-tab-item" data-key="${i}">
          ${title}
        </div>
        `
      }

      let childrenBodyPart = children
        .map((item, j) => {
          return `
            <div data-key="${i}-${j}">
              ${item.content}
            </div>
          `
        })
        .join('')

      bodyPart += `
      <div data-key="${i}">
        ${content}
      </div>
      ${childrenBodyPart}
      `
    }

    let html = `
    <div class="asm-tabs ${className}">
      <div class="asm-tabs-tab-items">
        ${titlePart}
      </div>
      <div class="asm-tabs-tab-pane">
        ${bodyPart}
      </div>
    </div>
    `

    element.innerHTML += html
  }

  bindEvents() {
    let { element } = this
    let tabs = element.querySelector('.asm-tabs-tab-items')
    let tabItems = Array.from(element.querySelectorAll('.asm-tabs-tab-item'))
    let panes = element.querySelectorAll('.asm-tabs-tab-pane > div')

    const activeTabPane = (key) => {
      for (let i = 0; i < panes.length; i++) {
        let p = panes[i]
        let pk = p.dataset.key
        if (pk === key) {
          p.classList.add('active')
        } else {
          p.classList.remove('active')
        }
      }
    }

    const activeTab = (elt) => {
      let tabItems = Array.from(element.querySelectorAll('.asm-tabs-tab-item'))
      if (elt.classList.contains('asm-tabs-tab-item')) {
        for (let i = 0; i < tabItems.length; i++) {
          let t = tabItems[i]
          t.classList.remove('active')
        }

        elt.classList.add('active')
        let key = elt.dataset.key
        activeTabPane(key)

        if (elt.classList.contains('group')) {
          let parent = elt.parentElement
          if (parent.classList.contains('expanded')) {
            parent.classList.remove('expanded')
          } else {
            parent.classList.add('expanded')
          }
        }
      }
    }

    tabs.addEventListener('click', (e) => {
      const elt = e.target
      if (elt.classList.contains('asm-tabs-tab-item')) {
        activeTab(elt)
      }
    })

    if (tabItems.length > 0) {
      let firstTab = tabItems[0]
      let firstKey = firstTab.dataset.key
      activeTab(firstTab)
      activeTabPane(firstKey)
    }
  }
}

export default Tabs
