export const stringIsDigit = function (s: string) {
  const l = s.length
  const a = '0123456789'
  for (let i = 0; i < l; i += 1) {
    if (a.indexOf(s[i]) < 0) {
      return false
    }
  }

  return true
}

export const debounce = function (func, wait) {
  let timeout

  let debounced = function(...args) {
    if (timeout) clearTimeout(timeout)
    timeout = setTimeout(() => {
      func(...args)
    }, wait)
  }

  return debounced
}

export const reallocateIndent = function (lines: string[]) {
  let res = ''
  let indent = 0

  for (let l of lines) {
    l = l.trim()
    if (l.startsWith('@')) {
      res += l + '\n'
      indent = 2
    } else if (l.startsWith('.return')) {
      indent = 0
      res += `  ${l}\n`
    } else {
      res += `${' '.repeat(indent)}${l}\n`
    }
  }

  return res
}
