export const register_mapper = function () {
  return {
    pa: 0b00000000,
    a1: 0b00010000,
    a2: 0b00100000,
    a3: 0b00110000,
    c1: 0b01000000,
    f1: 0b01010000,
  }
}

export const instruction_mapper = function () {
  return {
    set: 0b00000000,
    load: 0b00000001,
    add: 0b00000010,
    save: 0b00000011,
    compare: 0b00000100,
    jump_if_less: 0b00000101,
    jump: 0b00000110,
    save_from_register: 0b00000111,

    set2: 0b00001000, // 用于给寄存器存一个 16 位数字
    load2: 0b00001001, // 用于把内存中的一个 16 字节的数字读取到寄存器中
    add2: 0b00001010, // 用于把 16 位寄存器相加
    save2: 0b00001011, // 用于把 16 位寄存器的数字存到内存地址中，内存地址是 16 位数字
    subtract2: 0b00001100, // 用于把 16 位寄存器相减然后保存（具体用法和 add2 一样）
    load_from_register: 0b00001101,
    load_from_register2: 0b00001110,
    save_from_register2: 0b00001111,
    jump_from_register: 0b00010000,

    shift_right: 0b00010001,
    and: 0b00010011,

    multiply2: 0b00010100,
    // 适配作业 7 函数例子

    halt: 0b11111111,
  }
}
